const express = require("express");
const volleyball = require("volleyball");
const cors = require("cors");

const mongoose = require("mongoose");
const { default: Chocolate } = require("./model/chocolateModel");
const { default: chocolatesRouter } = require("./routes/chocolatesRoutes");

const app = express();
const port = process.env.PORT || 3000;

main().catch(error => console.log(error))
async function main(){
   await mongoose.connect("mongodb://localhost:27017/chocolate_db");
    console.log(`üî• Database connected successfully üî•`); 
}

app.use(express.urlencoded({extended: false}));
app.use(express.json());

app.use(cors());
app.use(volleyball)

app.use("/",chocolatesRouter);

app.listen(port, () => {
    console.log("üõ†Ô∏è  Server Open üõ†Ô∏è")
});